import { Nullable } from "primereact/ts-helpers"
import { useState } from 'react'
import Button from '../../components/buttons/Button'
import { InputComponent } from '../../components/inputs/InputComponent'
import { TextareaComponent } from '../../components/inputs/TextareaComponent'
import { CustomCalendar } from '../../components/inputs/calendar/CustomCalendar'
import { SelectComponent } from '../../components/selects/SelectComponent'
import { PersonalInfoComponent } from '../../features/register/PersonalInfoComponent'
import { useSaveInfo } from '../../features/register/hooks'
import './signUp.css'

export const SignUp = () => {
    const [date, setDate] = useState<Nullable<Date>>(null);

    const {
        fullName,
        clickSaveHandler,
        isInvalid,
        birthDate,
        gender,
        setBirthDate,
        setFullName,
        setGender,
        setUniversity,
        setDuties,
        setWorkName,
        setYearEnding
    } = useSaveInfo()

    return (
        <div className="signUp-container">
            <h1></h1>
            <div className='signUp-main'>
                <PersonalInfoComponent headTitle=' '>
                    <InputComponent
                        headTitle=''
                        placeholder=' '
                        required={true}
                        isInvalid={!fullName && isInvalid}
                        onChange={event => setFullName(event.target.value)} />
                    <div className='sign-justify'>
                        <SelectComponent
                            required
                            headTitle=''
                            isInvalid={!gender && isInvalid}
                            options={['', '']}
                            onChange={value => setGender(value)}
                            placeholder=' ' />
                        <CustomCalendar
                            headTitle=' '
                            isInvalid={!birthDate && isInvalid}
                            required
                            onChange={date => setBirthDate(date)} />
                    </div>
                </PersonalInfoComponent>

                <PersonalInfoComponent headTitle=''>
                    <div className='sign-justify'>
                        <SelectComponent headTitle='' options={['', '', '']} onChange={value => setUniversity(value)} placeholder=' ' />
                        <div className='calendar-year'>
                            <CustomCalendar headTitle=' ' dateFormat='yy' onChange={(date) => setYearEnding(date)} />
                        </div>
                    </div>
                </PersonalInfoComponent>

                <PersonalInfoComponent headTitle=''>
                    <SelectComponent headTitle=' ' options={['Google', 'Yandex', 'Facebook', 'Rambler']} onChange={value => setWorkName(value)} placeholder=' ' />
                    <TextareaComponent onChange={event => setDuties(event.target.value)} placeholder=' ' />

                    <div style={{ textAlign: 'center', marginTop: '4px' }}>
                        <Button onClick={clickSaveHandler} className='button-black'></Button>
                    </div>
                </PersonalInfoComponent>
            </div>
        </div>
    )
}